.header {
  background-color: #f8f8f8;
}
.header__top {
  padding: 10px 0 13px;
}
.header__bottom {
  border-top: 1px solid #e9ebf0;
  border-bottom: 1px solid #e9ebf0;
}
.header__wrap {
  display: flex;
  align-items: center;
  position: relative;
}
.header .logo svg {
  width: 192px;
  height: 54px;
}
.header .phone {
  font-size: 19px;
  font-weight: 700;
  color: #2e97df;
  margin-left: auto;
}
.header .btn {
  background-color: #1778bb;
  color: #fff;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 700;
  padding: 9px 14px;
  border-radius: 100px;
  margin-left: 20px;
}
.header .sites {
  margin-left: 26px;
}
.header .sites__toggle {
  font-size: 13px;
  font-weight: 400;
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}
.header .sites__toggle:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 20px;
  height: 20px;
  background-image: url("../img/icons/blue-arrow.png");
  background-size: 8px 5px;
  background-position: center;
  background-repeat: no-repeat;
  border: 2px solid #1778bb;
  border-radius: 100px;
}
.header .socials {
  display: flex;
  margin-left: 24px;
}
.header .socials__item {
  width: 36px;
  height: 36px;
}
.header .socials__item:not(:first-of-type) {
  margin-left: 8px;
}
.header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .nav__item {
  color: #1778bb;
  font-weight: 700;
  font-size: 13px;
  text-transform: uppercase;
  padding: 18px 28px;
  position: relative;
  border-left: 1px solid #e9ebf0;
}
.header .nav__item.green {
  color: #1db479;
}
.header .nav__item:first-of-type {
  padding-left: 35px;
}
.header .nav__item:last-of-type {
  border-right: 1px solid #e9ebf0;
  padding-right: 35px;
}
.header .burger {
  position: relative;
  display: none;
  right: 0;
  top: 0;
  width: 30px;
  height: 21px;
  z-index: 6;
  border-radius: 100px;
}
.header .burger:before {
  content: '';
  position: absolute;
  box-shadow: 0 0 0 0 #33343d;
  transition: 1s;
  width: 2px;
  height: 2px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  display: block;
}
.header .burger.active:before {
  box-shadow: 0 0 0 5000px #33343d;
}
.header .burger.active span {
  display: none;
}
.header .burger.active svg {
  display: block;
}
.header .burger span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 3px;
  background-color: #33343d;
  display: block;
  border-radius: 100px;
  transition: 0.5s;
  transform: rotate(0);
  opacity: 1;
}
.header .burger span:nth-of-type(1) {
  bottom: auto;
}
.header .burger span:nth-of-type(3) {
  top: auto;
}
.header .burger svg {
  display: none;
  fill: #fff;
  width: 20px;
  height: 20px;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  margin: auto;
}
.header .mobile-menu {
  position: fixed;
  top: 70px;
  right: 0;
  height: calc(100vh - 50px);
  overflow: auto;
  width: 100%;
  z-index: 6;
  padding: 0 40px 40px;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-20px);
  transition: 0.5s;
  transition-delay: 0.2s;
}
.header .mobile-menu.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.header .mobile-menu .nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}
.header .mobile-menu .nav__item {
  border: none;
  color: #fff;
  padding: 20px 0;
  position: relative;
}
.header .mobile-menu .nav__item:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 103px;
  height: 2px;
  background-color: #fff;
}
.header .mobile-menu .nav__item.phone {
  display: block;
  color: #e05473;
  font-size: 20px;
  line-height: 1.5;
}
.header .mobile-menu .nav__item.mail {
  display: block;
  font-size: 20px;
  line-height: 1.5;
}
.header .mobile-menu .nav__item.address {
  display: block;
  font-size: 17px;
  line-height: 1.5;
  max-width: 535px;
}
.header .mobile-menu .nav__item.time {
  display: block;
  font-size: 15px;
  line-height: 1.5;
}
.footer {
  padding: 0 0 30px;
}
.footer__wrap {
  display: flex;
