}
.footer .clouds__item:nth-of-type(1) {
  top: 9px;
  left: 78px;
}
.footer .clouds__item:nth-of-type(2) {
  bottom: 0;
  left: 148px;
}
.footer .clouds__item:nth-of-type(3) {
  top: 7px;
  left: 266px;
}
.footer .clouds__item:nth-of-type(4) {
  top: 0;
  right: 309px;
}
.footer .clouds__item:nth-of-type(5) {
  right: 238px;
  bottom: 0;
}
.footer .clouds__item:nth-of-type(6) {
  right: 121px;
