}
@media screen and (max-width: 1200px) {
  .footer .clouds__item:nth-of-type(1),
  .footer .clouds__item:nth-of-type(2),
  .footer .clouds__item:nth-of-type(3) {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .footer__wrap {
    justify-content: space-between;
  }
  .footer .clouds {
    display: none;
  }
  .footer .logo {
    margin-left: 80px;
    margin-right: 0;
  }
}
@media screen and (max-width: 576px) {
  .footer .logo {
    display: none;
  }
}
.popup {
  position: fixed;
  background-color: rgba(51, 52, 60, 0.8);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none;
  z-index: 5;
}
.popup__wrap {
  display: flex;
  width: 100%;
  height: 100%;
  padding: 20px;
  justify-content: center;
  align-items: center;
}
.popup__item {
  max-width: 554px;
  width: 100%;
  border: 4px solid #33343d;
  position: relative;
  background-color: #fff;
}
.popup__item:before {
  content: '';
  position: absolute;
  left: 3px;
  top: 3px;
  width: calc(100% + 7px);
  height: calc(100% + 7px);
  background-color: #33343d;
  z-index: -1;
}
.popup__item-top {
  border-bottom: 4px solid #33343d;
  position: relative;
  padding-right: 54px;
}
.popup__item .name {
  display: flex;
  align-items: center;
  padding: 20px 25px 16px;
}
.popup__item .name h2 {
  margin: 0 15px 0 0;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  white-space: nowrap;
}
.popup__item .name .lines {
  border-top: 2px solid #33343d;
  border-bottom: 2px solid #33343d;
  width: 100%;
  height: 12px;
  margin-top: -4px;
}
.popup__item .close {
  position: absolute;
  right: 0;
  top: 0;
  width: 54px;
  height: 100%;
  border-left: 4px solid #33343d;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.popup__item .close svg {
  fill: #33343d;
  width: 20px;
  height: 20px;
}
.popup__item-content {
  text-align: center;
  padding: 53px 20px 37px;
}
.popup__item-content .text {
  margin: 0 0 31px;
}
.popup__item-content .text span {
  text-transform: uppercase;
}
.popup__item-content .btn {
  background-color: #ffa9bc;
  border: 2px solid #33343d;
  font-size: 14px;
  padding: 11px 16px 9px;
  display: inline-block;
}
.services__wrap {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  padding-bottom: 14px;
  position: relative;
}
.services__wrap:after {
  position: absolute;
  content: '';
  bottom: 0;
  left: 15px;
  width: calc(100% - 30px);
  height: 1px;
  background-color: #e9ebf0;
}
.services__item {
  margin: 0 15px 30px;
  width: calc((100% / 3) - 30px);
  display: flex;
  background-color: #2e97df;
  padding: 30px 30px 25px;
}
.services__item .content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.services__item .category {
  color: #fff;
  font-size: 10px;
  text-transform: uppercase;
  font-weight: 400;
  margin-bottom: 11px;
}
.services__item .name {
  font-size: 15px;
  font-weight: 700;
  line-height: 1.2;
  color: #fff;
  margin: 0 0 14px;
}
.services__item .link {
  font-size: 10px;
  font-weight: 800;
  text-transform: uppercase;
  color: #fff;
  border: 1px solid #fff;
  position: relative;
  padding: 7px 30px 7px 10px;
  left: -5px;
  margin-top: auto;
}
.services__item .link:after,
.services__item .link:before {
  content: '';
  position: absolute;
  width: 12px;
  height: 2px;
  background-color: #fff;
  top: 0;
  right: 8px;
  bottom: 0;
  margin: auto;
}
.services__item .link:after {
  transform: rotate(90deg);
}
.services__item .icon svg {
  width: 110px;
  height: 110px;
}
.intro__bg {
  background: linear-gradient(-135deg, #95d2ea 0%, #71c4ec 100%);
  border: 4px solid #33343d;
  height: 471px;
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
}
.intro__bg:before {
  content: '';
  position: absolute;
  left: 3px;
  top: 3px;
  width: calc(100% + 7px);
  height: calc(100% + 7px);
  background-color: #33343d;
  z-index: -1;
}
.intro .man {
  position: absolute;
  left: 111px;
  top: 143px;
  animation: man 1.5s infinite steps(10);
  animation-direction: alternate;
  z-index: 3;
}
.intro .man svg {
  width: 198px;
  height: 162px;
}
.intro .clouds {
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.intro .clouds__layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.intro .clouds__layer[data-layer="1"] {
  z-index: 1;
}
.intro .clouds__layer[data-layer="1"] svg {
  width: 30px;
  height: 18px;
}
.intro .clouds__layer[data-layer="1"] svg:nth-of-type(1) {
  top: 50px;
  right: -100px;
  animation-duration: 5s;
}
.intro .clouds__layer[data-layer="1"] svg:nth-of-type(2) {
  bottom: 100px;
  right: -100px;
  animation-duration: 5s;
  animation-delay: 0.5s;
}
.intro .clouds__layer[data-layer="1"] svg:nth-of-type(3) {
  top: 100px;
  right: -100px;
  animation-duration: 5s;
  animation-delay: 2s;
}
.intro .clouds__layer[data-layer="1"] svg:nth-of-type(4) {
  bottom: 50px;
  right: -100px;
  animation-duration: 5s;
  animation-delay: 2.5s;
}
.intro .clouds__layer[data-layer="2"] {
  z-index: 2;
}
.intro .clouds__layer[data-layer="2"] svg {
  width: 60px;
  height: 37px;
}
.intro .clouds__layer[data-layer="2"] svg:nth-of-type(1) {
  top: 150px;
  right: -100px;
  animation-duration: 4s;
}
.intro .clouds__layer[data-layer="2"] svg:nth-of-type(2) {
  bottom: 150px;
  right: -100px;
  animation-duration: 4s;
  animation-delay: 1.5s;
}
.intro .clouds__layer[data-layer="3"] {
  z-index: 3;
}
.intro .clouds__layer[data-layer="3"] svg {
  width: 90px;
  height: 54px;
}
.intro .clouds__layer[data-layer="3"] svg:nth-of-type(1) {
  top: 110px;
  right: -100px;
  animation-duration: 3s;
}
.intro .clouds__layer[data-layer="3"] svg:nth-of-type(2) {
  bottom: 110px;
  right: -100px;
  animation-duration: 3s;
  animation-delay: 1.5s;
}
.intro .clouds__item {
  position: absolute;
  animation: cloud infinite steps(30);
}
.intro .title {
  font-size: 30px;
  font-weight: 400;
  width: 50%;
  margin-left: auto;
  line-height: 1.7;
  padding: 0 85px 0 41px;
  z-index: 5;
}
@keyframes man {
  from {
    top: 143px;
  }
  to {
    top: 163px;
  }
}
@keyframes cloud {
  from {
    right: -100px;
  }
  to {
    right: calc(100% + 100px);
  }
}
@media screen and (max-width: 1200px) {
  .intro__bg {
    height: 400px;
  }
  .intro .man {
    top: 100px;
  }
  .intro .title {
    padding: 0 20px 0 0;
  }
  @keyframes man {
    from {
      top: 100px;
    }
    to {
      top: 120px;
    }
  }
}
@media screen and (max-width: 992px) {
  .intro__bg {
    height: 350px;
  }
  .intro .man {
    top: 80px;
    left: 20px;
  }
  .intro .title {
    padding: 0 20px 0 0;
    font-size: 24px;
  }
  @keyframes man {
    from {
      top: 80px;
    }
    to {
      top: 100px;
    }
  }
}
@media screen and (max-width: 768px) {
  .intro .title {
    position: absolute;
    left: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(51, 51, 61, 0.7);
    color: #fff;
    padding: 40px;
  }
}
@media screen and (max-width: 576px) {
  .intro .title {
    font-size: 20px;
    padding: 40px 20px;
  }
}
